/* stylelint-enable */
.page-header {
  background-color: #ffffff;
}
.page-footer {
  background-color: #F2F2F2;
}
.page {
  height: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
}
.page__body {
  min-height: 100%;
  min-width: 320px;
}
.site-list {
  font-size: 20px;
  line-height: 20px;
}
.site-list a {
  text-decoration: none;
  color: black;
  text-transform: uppercase;
  font-weight: 500;
}
.site-list li {
  list-style-type: none;
}
.promo {
  background: linear-gradient(rgba(104, 183, 56, 0.85) 63%, #ffffff 63%), url("../img/cat.png");
  height: 302px;
  width: 320px;
  background-repeat: no-repeat;
  position: relative;
}
.promo ::before {
  content: "";
  position: absolute;
  top: 70px;
  left: 130px;
  background: url("../img/glasswitheat.png");
  background-repeat: no-repeat;
  z-index: 2;
  height: 280px;
  width: 270px;
}
.promo__program-search {
  display: block;
  margin-top: 180px;
  width: 140px;
  top: 220px;
  color: #ffffff;
  padding: 7px 44px 7px 44px;
  background: #68B738;
  text-decoration: none;
}
.promo__text {
  color: #ffffff;
  font-size: 14px;
  line-height: 18px;
  font-weight: normal;
}
.promo__header {
  font-size: 36px;
  line-height: 36px;
  font-weight: normal;
}
.promo__slogan {
  font-size: 14px;
  line-height: 14px;
  font-weight: normal;
  text-transform: uppercase;
}
.cat-program__item {
  background-color: #F2F2F2;
}
.cat-program__title {
  font-size: 24px;
  line-height: 37px;
  font-weight: normal;
  color: #000000;
  position: relative;
}
.cat-program__loosing::before {
  content: "";
  position: absolute;
  background-image: url("../img/cat_back.svg");
  background-repeat: no-repeat;
  width: 50px;
  height: 53px;
  background-position: 0 0;
  left: 130px;
}
.cat-program__gaing::before {
  content: "";
  position: absolute;
  background-image: url("../img/cat_strong.svg");
  background-repeat: no-repeat;
  width: 67px;
  height: 50px;
  background-position: 0 0;
  left: 130px;
}
.cat-program__text {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: normal;
  color: #444444;
}
.cat-program__catalog {
  font-size: 16px;
  line-height: 16px;
  color: #000000;
  text-decoration: none;
  position: relative;
}
.cat-program__catalog ::before {
  content: "";
  background-image: url("../img/right_arrow.svg");
  background-repeat: no-repeat;
  left: 80px;
  top: 8px;
  position: absolute;
  width: 32px;
  height: 10px;
  background-position: 0 0;
}
.description__title {
  color: #000000;
  font-size: 36px;
  line-height: 40px;
  font-weight: normal;
}
.description__list {
  list-style-type: none;
}
.description__text {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  font-weight: normal;
}
.example {
  background-color: #EAEAEA;
}
.example__title {
  font-size: 36px;
  line-height: 40px;
  font-weight: normal;
}
.example__description {
  font-family: "Arial", sans-serif;
  color: #444444;
  font-size: 14px;
  line-height: 18px;
}
.example__promo {
  border-color: #c4c4c4;
}
.example__promo p {
  color: #000000;
  font-size: 24px;
  line-height: 24px;
}
.example__promo legend {
  color: #c4c4c4;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  border-radius: 3px;
}
.example__epenses {
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
}
.example__change {
  color: #000000;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}
.cooperation__invitation {
  color: #111111;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
}
.cooperation__adress {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: normal;
  font-style: normal;
}
.social__list {
  list-style-type: none;
}
.copyright__link {
  text-decoration: none;
  color: #444444;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
}
